import { sessionStorage, storage, guid } from "@kiner/taro-track-corejs";
export class Constant {
    constructor() {
        this.session = "sid";
        this.guid = "guid";
    }
    getSessionID() {
        let session = sessionStorage(this.session);
        if (!session) {
            session = guid();
            sessionStorage(this.session, session);
        }
        return session;
    }
    getDistinctID() {
        let juid = storage(this.guid);
        if (!juid) {
            juid = guid();
            storage(this.guid, juid);
        }
        return juid;
    }
}
