import { Logger } from '..';
const logger = new Logger("code/wx-tools");
export function getWxReferrer() {
    const pages = getCurrentPages().map(item => item.route);
    logger.info(`taro-track:getWxReferrer`, pages);
    return pages.length === 1 ? '' : pages[pages.length - 2];
}
export function getWxCurrentHref() {
    const pages = getCurrentPages().map(item => item.route);
    return pages[pages.length - 1];
}
export function getWxCurrentPagesStack() {
    return getCurrentPages();
}
export function getWxNetwork() {
    return new Promise(resolve => {
        wx.getNetworkType({
            success(result) {
                resolve(result.networkType);
            }
        });
    });
}
export function getWxSystemInfo() {
    let os = wx.getSystemInfoSync();
    return {
        osInfo: function () {
            return `${os.system}`;
        },
        wechatInfo: function () {
            return `${os.version} ${os.SDKVersion}`;
        },
        langInfo: function () {
            return `${os.language}`;
        },
        originalOs: os
    };
}
export function storage(key, val) {
    if (val === undefined) {
        return wx.getStorageSync(key);
    }
    else {
        wx.setStorageSync(key, val);
    }
}
export const sessionStorageTimeKey = 'SESSION_STORAGE_DATE_';
export const sessionStorageKey = 'SESSION_STORAGE_';
export function sessionStorage(key, val, expired) {
    const expiredKey = `${sessionStorageTimeKey}${key}`;
    const valKey = `${sessionStorageKey}${key}`;
    if (val === undefined) {
        const expiredTime = storage(expiredKey);
        if (expiredTime) {
            const date = new Date();
            date.setTime(parseInt(expiredTime));
            if (date.getTime() - Date.now() <= 0) {
                storage.remove(valKey);
                storage.remove(expiredKey);
                return null;
            }
            return storage(valKey);
        }
        return storage(valKey);
    }
    else {
        storage(valKey, val);
        storage(expiredKey, expired);
    }
}
storage.remove = function (key) {
    wx.removeStorageSync(key);
};
let fsm;
export function getFileSystemManager() {
    return fsm ? fsm : (fsm = wx.getFileSystemManager());
}
export function toggleAppShow(cb) {
    return new Promise(resolve => {
        wx.onAppShow(() => {
            cb(true);
            resolve(true);
        });
        wx.onAppShow(() => {
            cb(true);
            resolve(false);
        });
    });
}
let wxRequest, wxUpload, wxDownload;
export function getWxRequest() {
    return wxRequest ? wxRequest : (wxRequest = wx.request);
}
export function overrideWxRequest(newRequest) {
    Object.defineProperty(wx, "request", { value: newRequest });
}
export function getWxUpload() {
    return wxUpload ? wxUpload : (wxUpload = wx.uploadFile);
}
export function overrideWxUpload(newUpload) {
    Object.defineProperty(wx, "uploadFile", { value: newUpload });
}
export function getWxDownload() {
    return wxDownload ? wxDownload : (wxDownload = wx.downloadFile);
}
export function overrideWxDownload(newDownload) {
    Object.defineProperty(wx, "downloadFile", { value: newDownload });
}
export function getWxNetworkType(options) {
    return new Promise((resolve, reject) => {
        wx.getNetworkType({
            success(opts) {
                resolve(opts);
                options && options.success(opts);
            },
            fail(opts) {
                reject(opts);
                options && options.fail(opts);
            }
        });
    });
}
export function onWxNetworkStatusChange(cb) {
    wx.onNetworkStatusChange(result => {
        cb(result);
    });
}
const lineAndColNumReg = /\(.*\:(\d+)\:(\d+)\)$/gi;
export function resolveErrorMessageLineAndColNum(str) {
    const res = lineAndColNumReg.exec(str);
    if (res) {
        return {
            line: parseInt(res[1]),
            column: parseInt(res[2])
        };
    }
    else {
        return {
            line: -1,
            column: -1
        };
    }
}
export function parseErrorMessage(error) {
    if (!error || !error.split) {
        return {
            message: error,
            source: error,
            line: -1,
            column: -1
        };
    }
    const errors = error.split('\n');
    const { line, column } = resolveErrorMessageLineAndColNum(errors.filter(item => item.match(/\S*at/))[0]);
    let message = errors[0];
    let source = error;
    return {
        message,
        source,
        line,
        column
    };
}
export function compareVersion(v1, v2) {
    const v1Arr = v1.split('.');
    const v2Arr = v2.split('.');
    const len = Math.max(v1Arr.length, v2Arr.length);
    while (v1Arr.length < len) {
        v1Arr.push('0');
    }
    while (v2.length < len) {
        v2Arr.push('0');
    }
    for (let i = 0; i < len; i++) {
        const num1 = parseInt(v1Arr[i]);
        const num2 = parseInt(v2Arr[i]);
        if (num1 > num2) {
            return 1;
        }
        else if (num1 < num2) {
            return -1;
        }
    }
    return 0;
}
export function canIUse(targetVersion) {
    const version = wx.getSystemInfoSync().SDKVersion;
    return compareVersion(version, targetVersion) >= 0;
}
