import { TaroTrackLoggerNamespace } from '@kiner/taro-track-common';
import { Logger4Node } from '@kiner/taro-track-corejs';
const logger = Logger4Node.create(TaroTrackLoggerNamespace, {
    groupCollapsed: true
});
export class TransporterConsole {
    constructor() { }
    send(data, callback) {
        logger.info('TransporterConsole.send', data);
        if (callback) {
            callback(null);
        }
    }
    post(dataArray, callback) {
        logger.info('TransporterConsole.post', ...dataArray);
        if (callback) {
            callback(null);
        }
    }
    push(data) {
        return new Promise(resolve => {
            logger.info('TransporterConsole', ...data);
            resolve();
        });
    }
    pipe(next) {
        return next;
    }
}
