export const SupportTyped = isSupportTyped();
export function encodeToUint8Array(data) {
    return zip(data, {});
}
const ArrayProto = Array.prototype;
export function encodeToArray(data) {
    if (SupportTyped) {
        return Array.from(encodeToUint8Array(data));
    }
    const binary = zip(data).join('');
    return ArrayProto.slice.call(binary).map(function (v) {
        return v.charCodeAt(0);
    });
}
