import { TaroTrackLoggerNamespace } from '@kiner/taro-track-common';
export class Logger {
    constructor(namespace) {
        this.namespace = namespace;
        Logger.logTpl = `${Logger.logTpl}(${namespace}):`;
    }
    static getMessage(message) {
        return `${Logger.logTpl}${message}`;
    }
    log(message, ...args) {
        if (!Logger.showLog)
            return;
        console.groupCollapsed(Logger.getMessage(message));
        console.log(...args);
        console.groupEnd();
    }
    error(message, ...args) {
        if (!Logger.showLog)
            return;
        console.groupCollapsed(Logger.getMessage(message));
        console.error(...args);
        console.groupEnd();
    }
    info(message, ...args) {
        if (!Logger.showLog)
            return;
        console.groupCollapsed(Logger.getMessage(message));
        console.info(...args);
        console.groupEnd();
    }
    warn(message, ...args) {
        if (!Logger.showLog)
            return;
        console.groupCollapsed(Logger.getMessage(message));
        console.warn(...args);
        console.groupEnd();
    }
}
Logger.logTpl = `[${TaroTrackLoggerNamespace}]`;
Logger.showLog = true;
