import Marker from "./mark";
export default function (array) {
    if (array.length < 1) {
        return "";
    }
    let o1;
    let o2;
    let o3;
    let h1;
    let h2;
    let h3;
    let h4;
    let bits;
    let i = 0;
    let ac = 0;
    let enc = "";
    let tmp_arr = [];
    do {
        o1 = array[i++];
        o2 = array[i++];
        o3 = array[i++];
        bits = o1 << 16 | o2 << 8 | o3;
        h1 = bits >> 18 & 0x3f;
        h2 = bits >> 12 & 0x3f;
        h3 = bits >> 6 & 0x3f;
        h4 = bits & 0x3f;
        tmp_arr[ac++] = Marker.charAt(h1) + Marker.charAt(h2) + Marker.charAt(h3) + Marker.charAt(h4);
    } while (i < array.length);
    enc = tmp_arr.join("");
    switch (array.length % 3) {
        case 1:
            enc = enc.slice(0, -2) + "==";
            break;
        case 2:
            enc = enc.slice(0, -1) + "=";
            break;
    }
    return enc;
}
