import { ListenerEventCallbackType, ListenerEventType } from "../inter/listener";
import { NQueue as Queue } from './Queue';
import { getWxCurrentHref, parseErrorMessage, toggleAppShow } from '@kiner/taro-track-corejs';
class EventEmitter {
    constructor() {
        this.callbacks = {};
    }
    getCallbacks(event) {
        return this.callbacks[event] || (this.callbacks[event] = []);
    }
    on(event, callback) {
        const callbacks = this.getCallbacks(event);
        if (callbacks.indexOf(callback) < 1) {
            callbacks.push(callback);
        }
        return this;
    }
    emit(event, ...data) {
        const callbacks = this.getCallbacks(event);
        for (const callback of callbacks) {
            callback(data[0], data[1], data[2], data[3]);
        }
        return this;
    }
}
function consoleProxy(cb) {
    const { log, error } = console;
    console.error = function (...args) {
        cb('error', args[0]);
        error.call(console, ...args);
    };
}
export class WechatListener {
    constructor(options) {
        const opts = options || {};
        this.root = opts.root;
        this.types = opts.types || [
            ListenerEventType.Click,
            ListenerEventType.Change
        ];
        this.queue = new Queue({ size: opts.size || 6 });
        this.emitter = new EventEmitter();
        this.inlitialize();
    }
    inlitialize() {
        setTimeout(() => {
            this.emitter.emit(ListenerEventCallbackType.OnPageView, getWxCurrentHref());
        }, 0);
        consoleProxy((type, error) => {
            const onWindowErrorBackpass = parseErrorMessage(error);
            if (type === "error") {
                const backpass = this.queue.get();
                this.queue.reset();
                this.emitter.emit(ListenerEventCallbackType.OnUIError, {
                    backpass,
                    error: onWindowErrorBackpass
                });
            }
        });
        this.listenVisiableChange();
    }
    getListenerBackpass(event) {
        const target = event.target;
        const tag = target.tagName.toLowerCase();
        return {
            tag,
            eventType: event.type,
            url: location.href,
            cssPath: "",
            mark: ""
        };
    }
    listenVisiableChange() {
        toggleAppShow((isShow => {
            this.emitter.emit(ListenerEventCallbackType.OnVisiable, !isShow);
        }));
    }
    on(event, callback) {
        this.emitter.on(event, callback);
    }
}
