export class NextableIteratorProtocolWrapper {
    constructor(node) {
        this.node = node;
    }
    next() {
        const c = this.node;
        if (c === null) {
            return { done: true, value: null };
        }
        this.node = c.next;
        return {
            done: false,
            value: c.data
        };
    }
}
